let cachedAddress = null;
async function getAddress(coordenates) {
    const lat = coordenates[0];
    const lng = coordenates[1];
    if (cachedAddress) {
        console.log(`Cached address found: ${cachedAddress}`)
        return cachedAddress
    } else {
        try {
            console.log(`Cached address not found, making API call:`)
            const data = await fetch(`https://ax7m3xfbei.execute-api.sa-east-1.amazonaws.com/ReverseGeocode?lat=${lat}&lng=${lng}`);
            const dataJson = await data.json();
            const city = dataJson.address.city;
            const country = dataJson.address.country;
            cachedAddress = `${city} ${country}`;
            return cachedAddress
        } catch(error) {
            console.log(error)
        };
    };
};

export { getAddress }