function formattedDate(date) {
    const months = [
        'January', 
        'February', 
        'March', 
        'April', 
        'May', 
        'June', 
        'July', 
        'August', 
        'September', 
        'October', 
        'November', 
        'December'
    ];
    const daySuffixCalc = (day) => {
        if (day > 3 && day < 21) return 'th';
        switch (day % 10) {
            case 1: return 'st';
            case 2: return 'nd';
            case 3: return 'rd';
            default: return 'th';
        }
    };
    const monthNumber = date.slice(-5, -3);
    const day = parseInt(date.slice(8, 10));
    const month = months[parseInt(monthNumber -1)];
    const year = date.slice(0, 4);
    const daySuffix = daySuffixCalc(day);
    const fullDate = `${month} ${day}${daySuffix}, ${year}`;
    return fullDate;
};

function formattedHour(time) {
    const unformattedHour = time;
    const amPm = (hour) => {
        return hour.toString().split(':')[0] >= 12 ? 'pm' : 'am';
    };
    let hours = unformattedHour.slice(0, 2);
    const hourSuffix = amPm(hours);
    hours = hours % 12;
    hours = hours ? hours : 12;
    const minutes = unformattedHour.slice(-2, time.length);
    const fullHour = `${hours}:${minutes} ${hourSuffix}`
    return fullHour;
};

async function returnFullDate(timezone) {
    const location = timezone;
    try{
        const response = await fetch(`https://4sbnot37bb.execute-api.sa-east-1.amazonaws.com/get-date?timezone=${location}`)
        if (!response.ok) {
            const error = new Error(response.status);
            error.status = response.status
            throw error
        };
        const data = await response.json();
        const dataArray = data.formatted.split(' ');
        const date = dataArray[0];
        const time = dataArray[1].slice(0, 5);
        const fullDate = `${formattedDate(date)}\r\n${formattedHour(time)}`;
        return fullDate
    } catch(error) {
        const response = await fetch(`http://worldtimeapi.org/api/timezone/${location}`);
        const data = await response.json();
        const dataArray = data.datetime.slice(0, 16).split('T');
        const date = dataArray[0];
        const time = dataArray[1];
        if (!response.ok) {
            return ' '
        };
        const fullDate = `${formattedDate(date)}\r\n${formattedHour(time)}`;
        return fullDate;
    };
};

function preloader() {
    const repeatedPreloader = document.querySelector('.preloader-container');
    if (repeatedPreloader) {
        repeatedPreloader.remove();
    };
    const page = document.querySelector('body');
    const preloaderContainer = document.createElement('div');
    preloaderContainer.classList.add('preloader-container');
    page.appendChild(preloaderContainer);
};

function removePreloader() {
    const preloaderContainer = document.querySelector('.preloader-container');
    if (preloaderContainer) {
        preloaderContainer.classList.add('preloader-hidden');
        preloaderContainer.addEventListener('transitionend', () => {
            preloaderContainer.remove()
        });
        setTimeout(() => {
            if (document.contains(preloaderContainer)) {
                preloaderContainer.remove();
            };
        }, 500)
    };
};

async function displayPrimaryData(object, measurementUnit) {
    const mainData = document.querySelector('.main-data-container');

    const data = document.createElement('div');
    data.classList.add('primary-data-content');
    let temperatureUnit = measurementUnit === 'metric' ? '°C' : '°F';
    const locationArray = object.address;
    const timezone = object.timezone;

    const itemsArray = [
        'location', 
        'date', 
        'weather-description', 
        'current-temperature', 
        'current-weather-icon'
    ];

    const contents = [ 
        `${locationArray}`,
        await returnFullDate(timezone),
        object.currentConditions.conditions,
        `${object.currentConditions.temp} ${temperatureUnit}`,
    ];

    for (let i = 0; i < itemsArray.length; i++){
        const element = document.createElement('div');
        element.classList.add(`${itemsArray[i]}`);
        if (element.classList.contains('current-weather-icon')) {
            element.style.backgroundImage = `url(resources/icons/${object.currentConditions.icon}.png)`;
        } else {
            element.textContent = contents[i];
        }
        data.appendChild(element);
    };
    
    if (!mainData.querySelector('.primary-weather-data')){
        const dataContainer = document.createElement('div');
        dataContainer.classList.add('primary-weather-data');
        mainData.appendChild(dataContainer);
        dataContainer.appendChild(data)
        mainData.appendChild(dataContainer);
    } else {
        const dataContainer = document.querySelector('.primary-weather-data');
        const repeatedContainer = dataContainer.querySelector('.primary-data-content');
        if (repeatedContainer) {
            repeatedContainer.remove()
        };
        dataContainer.appendChild(data)
        mainData.appendChild(dataContainer);
    }
};

function displaySecondaryData(object, measurementUnit) {
    const mainContent = document.querySelector('.main-content');
    const secondaryData = document.createElement('div');
    secondaryData.classList.add('secondary-data-container');
    let temperatureUnit = measurementUnit === 'metric' ? '°C' : '°F';
    let speedUnit = measurementUnit === 'metric' ? 'kmp' : 'mph';

    const itemsArray = [
        'feels-like', 
        'humidity', 
        'chance-of-rain', 
        'wind-speed', 
        'cloud-cover'
    ];

    const description = [
        `Feels like`, 
        `Humidity`, 
        `Chance of rain`, 
        `Wind speed`, 
        `Cloud cover`
    ];

    const values = [
        `${object.currentConditions.feelslike} ${temperatureUnit}`,
        `${object.currentConditions.humidity} %`,
        `${object.currentConditions.precipprob} %`,
        `${object.currentConditions.windspeed} ${speedUnit}`,
        `${object.currentConditions.cloudcover} %`
    ];

    for (let j = 0; j < itemsArray.length; j++) {
        const container = document.createElement('div');
        container.classList.add('weather-info-container');
        const icon = document.createElement('div');
        icon.classList.add('weather-icon');

        const item = document.createElement('div');
        item.classList.add('weather-info');

        const title = document.createElement('p');
        title.classList.add(`${itemsArray[j]}`);
        title.textContent = description[j];

        const value = document.createElement('p');
        value.classList.add('value');
        value.textContent = values[j];

        item.append(title, value);
        container.append(icon, item);
        secondaryData.appendChild(container);
    };
    const repeatedContainer = mainContent.querySelector('.secondary-data-container');
    if (repeatedContainer) {
        repeatedContainer.remove()
    };
    mainContent.appendChild(secondaryData);
};

function setsContainerMargin(setsContainerClass, expClass) {
    const setsContainer = document.querySelector(setsContainerClass);
    const children = setsContainer.querySelector(expClass).children
    const setsContainerWidth = setsContainer.clientWidth;
    for (let child of children) {
        const childWidth = child.clientWidth;
        const margin = Math.floor((setsContainerWidth - childWidth) / 2);
        child.style.margin = `0 ${margin}px`
    };
};

function navButtonStyle(index, currentNavBarClass) {
    const currentNavBar = document.querySelector(currentNavBarClass);
    const buttons = currentNavBar.querySelectorAll('.navigation-button');
    if (index < 0 || index >= buttons.length) {
        console.log(`Index ${index} is out of bounds for buttons`);
        return;
    };
    for (let button of buttons) {
        button.classList.remove('navigation-button-active');
    };
    buttons[index].classList.add('navigation-button-active');
};

function getOrientation() {
    const isPortrait = window.matchMedia('(orientation: portrait)').matches;
    const keyboardVisible = window.innerHeight < screen.availHeight;

    if (isPortrait) {
        return 'portrait';
    } else if (!keyboardVisible) {
        return 'landscape';
    } else {
        return 'landscape';
    };
};

function setBackground(url) {
    const pageBody = document.querySelector('body');
    const photoUrl = url;
    pageBody.style.backgroundImage = `url('${photoUrl}')`;
    pageBody.style.backgroundSize = 'cover';
    pageBody.style.backgroundRepeat = 'no-repeat';
    pageBody.style.backgroundPosition = 'center';
};

function creditButton() {
    const repeatedCreditButton = document.querySelector('.credit-button');
    if (repeatedCreditButton) {
        repeatedCreditButton.remove();
    }
    const body = document.querySelector('body');
    const button = document.createElement('div');
    const link = document.createElement('a');
    button.classList.add('credit-button');
    link.setAttribute('href', 'https://github.com/adibast');
    link.target = '_blank';
    link.textContent = 'Adibast';
    button.appendChild(link);
    body.appendChild(button);
};

export { preloader, removePreloader, displayPrimaryData, displaySecondaryData, setsContainerMargin, navButtonStyle, setBackground, getOrientation, creditButton };