import { navButtonStyle } from "./renderModule.js";

let currentIndex = 0;
let dailyIndex = 0;
let hourlyIndex = 0;

function updateIndex(instruction, index) {
    if (instruction === 'daily') {
        dailyIndex = index
    } else {
        hourlyIndex = index
    }
};

function scroll(index, setsContainerClass) {
    const setsContainer = document.querySelector(setsContainerClass);

    if (!setsContainer) {
        console.error(`Container with class ${setsContainerClass} not found`);
        return;
    };

    const sets = setsContainer.children;

    if (index >= sets.length) {
        console.error(`Index ${index} is out of range for sets`);
        return;
    };
    currentIndex = index;
    
    const targetSet = sets[index];

    if (targetSet) {
        setTimeout(function (){
            targetSet.scrollIntoView({
                block: 'center',
                inline: 'center'
            });
        }, 1);

        setsContainerClass === '.daily-forecast' 
            ? navButtonStyle(index, '.daily-navigation-bar') & updateIndex('daily', index)
            : navButtonStyle(index, '.hourly-navigation-bar') & updateIndex('hourly', index);
    };
};

function scrollButtons(containerClass, navigationBarClass) {
    const buttons = ['left-button', 'right-button'];
    const container = document.querySelector(containerClass);
    const navBarClassChildren = document.querySelector(navigationBarClass).children

    for (let i = 0; i < buttons.length; i++) {
        const repeatedButton = container.querySelector(`.${buttons[i]}`);
        if (repeatedButton) {
            repeatedButton.remove();
        };
        const button = document.createElement('div');
        button.classList.add(buttons[i]);
        container.appendChild(button);
    };
};

function returnCurrentIndex(instruction) {
    if (instruction === 'daily') {
        return dailyIndex
    } else {
        return hourlyIndex
    }
};

export { scroll, scrollButtons, returnCurrentIndex }