function dailyForecast(object, unit) {
    const aditionalInformation = document.querySelector('.aditional-information');
    const dailyForecastContainer = document.createElement('div');
    dailyForecastContainer.classList.add('daily-forecast', 'information-set');
    const arrayLength = object.days.length;
    
    const repeatedContainer = aditionalInformation.querySelector('.daily-forecast');
    if (repeatedContainer) {
        repeatedContainer.remove()
    };

    const aditionalInformationWidth = aditionalInformation.clientWidth;
    const elementWidth = window.screen.availWidth >= 1920 ? 298 : 200;
    const containerCount = Math.floor(aditionalInformationWidth / elementWidth);
    let containerSet = null;

    for (let i = 0; i < arrayLength; i++) {
        const daysOfWeek = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        const date = new Date(object.days[i].datetime)

        const forecastDaily = document.createElement('div');
        forecastDaily.classList.add('forecast-daily', 'aditional-weather-element');

        const categoriesList = [
            'dayOfWeek', 
            'temperature', 
            'conditionsDescription',
            'icon'
        ]; 
        
        const values = [
            `${daysOfWeek[date.getDay()]}`, 
            `${object.days[i].temp} ${unit}`,
            `${object.days[i].conditions}`,
            `${object.days[i].icon}`,
        ];

        for (let c = 0; c < categoriesList.length; c++) {
            const category = document.createElement('div');
            category.classList.add(`${categoriesList[c]}`);

            if (category.classList.contains('icon')) {
                category.style.backgroundImage = `url(resources/icons/${object.days[i].icon}.png)`;
            } else {
                category.textContent = values[c];
            };
            forecastDaily.appendChild(category)
        };
        if (!containerSet || containerSet.children.length >= containerCount) {
            containerSet = document.createElement('div');
            containerSet.classList.add('daily-forecast-set');
            dailyForecastContainer.appendChild(containerSet);
        }
        containerSet.appendChild(forecastDaily)
    };
    aditionalInformation.appendChild(dailyForecastContainer);
};

function hourlyForecast(object, unit) {
    const aditionalInformation = document.querySelector('.aditional-information');
    const hourlyForecastContainer = document.createElement('div');
    hourlyForecastContainer.classList.add('hourly-forecast', 'information-set');

    const repeatedContainer = aditionalInformation.querySelector('.hourly-forecast');
    if (repeatedContainer) {
        repeatedContainer.remove()
    };


    const arrayLenght = object.days[0].hours.length;
    const valueContainerSize = window.screen.availWidth >= 1920 ? 298 : 200;
    const containerCount = Math.floor(aditionalInformation.clientWidth / valueContainerSize);
    
    const formatHour = (input) => {
        let dateTime = input.split(':');
        let hour = parseInt(dateTime[0]);
        let minute = dateTime[1];
        let amPm = hour >= 12 ? 'pm' : 'am';
        hour = hour % 12;
        hour = hour ? hour : 12;
        return `${hour}:${minute} ${amPm}`
    };
    
    let containerSet = null;

    for (let i = 0; i < arrayLenght; i++) {
        const forecastHourly = document.createElement('div');
        forecastHourly.classList.add('forecast-hourly', 'aditional-weather-element');
        const elementsList = ['hour', 'temperature', 'conditions', 'icon'];

        const values = [
            `${formatHour(object.days[0].hours[i].datetime)}`,
            `${object.days[0].hours[i].temp} ${unit}`,
            `${object.days[0].hours[i].conditions}`,
            `${object.days[0].hours[i].icon}`
        ];

        for (let j = 0; j < elementsList.length; j++) {
            const container = document.createElement('div');
            container.classList.add(`${elementsList[j]}`)
            if (container.classList.contains('icon')) {
                container.style.backgroundImage = `url(resources/icons/${object.days[0].hours[i].icon}.png)`;
            } else {
                container.textContent = values[j];
            };
            forecastHourly.appendChild(container);
        };

        if(!containerSet || containerSet.children.length >= containerCount) {
            containerSet = document.createElement('div');
            containerSet.classList.add('hourly-forecast-set');
            hourlyForecastContainer.appendChild(containerSet);
        };
        containerSet.appendChild(forecastHourly)
    };
    aditionalInformation.appendChild(hourlyForecastContainer);
};

function secondaryInformationButtons(containerClass) {
    const container = document.querySelector(containerClass);
    const repeatedContainer = container.querySelector('.unit-buttons');
    if (repeatedContainer) {
        repeatedContainer.remove()
    };
    const buttons = document.createElement('div')
    const dailyHourly = document.createElement('div');
    const mphKph = document.createElement('div');
    buttons.classList.add('unit-buttons');
    dailyHourly.classList.add('display-daily-hourly');
    mphKph.classList.add('display-mph-kph');
    dailyHourly.textContent = 'Display hourly forecast';
    mphKph.textContent = 'Change units to °F/mph';
    const searchContainer = document.createElement('div');
    const inputSearch = document.createElement('input');
    const searchButton = document.createElement('button');
    inputSearch.type = 'text';
    searchContainer.classList.add('search-container');
    inputSearch.classList.add('location-field');
    inputSearch.addEventListener('input', inputFieldFilter);
    inputSearch.placeholder = 'Type in a city and country';
    searchButton.classList.add('search-button');
    searchContainer.append(inputSearch, searchButton);
    buttons.append(dailyHourly, mphKph, searchContainer);
    container.appendChild(buttons);
};

function navigationButtons(navBarContainerClass, setsContainerClass) {
    const navBarContainer = document.querySelector(navBarContainerClass);
    const setsContainer = document.querySelector(setsContainerClass);
    const navBarUniqueClass = `${setsContainerClass.split('-')[0].split('.')[1]}-navigation-bar`;

    const existingNavBar = navBarContainer.querySelector(`.${navBarUniqueClass}`);
    if (existingNavBar) {
        existingNavBar.remove()
    };

    const buttonQty = setsContainer.children.length;
    const navigationBar = document.createElement('div');
    navigationBar.classList.add('navigation-bar', `${navBarUniqueClass}`);

    for(let i = 0; i < buttonQty; i++) {
        const button = document.createElement('div');
        button.classList.add('navigation-button');
        navigationBar.appendChild(button);
    };

    navBarContainer.appendChild(navigationBar);
};

function inputFieldFilter(event) {
    let value = event.target.value;
    value = value.replace(/[^A-Za-zÀ-ÖØ-öø-ÿñÑ '\-]/g, '');
    value = value
        .toLowerCase()
        .replace(/\b\w/g, char => char.toUpperCase());
    event.target.value = value;
};

export { dailyForecast, hourlyForecast, secondaryInformationButtons, navigationButtons, inputFieldFilter }