import { getCoordinates } from './coordinatesModule.js'
import { getAddress } from './adressModule.js'
import { returnObject } from './weatherModule.js'
import { returnImageUrl } from './photoUrlModule.js';
import { preloader, removePreloader, displayPrimaryData, displaySecondaryData, navButtonStyle, setsContainerMargin, setBackground, getOrientation, creditButton } from './renderModule.js';
import { dailyForecast, hourlyForecast, secondaryInformationButtons, navigationButtons, inputFieldFilter } from './appElementsModule.js';
import { windowEventListener, currentUnit, currentDisplay, updatedWeatherObject } from './eventListenerModule.js';
import { scroll, scrollButtons, returnCurrentIndex } from './interactionModule.js';

let status = {};
let codeExecuted = false;
let errorHandled = false;
let currentWeatherObject = null;

async function searchButtonEventListener() {
    const textInput = document.querySelector('.location-input-text');
    const location = textInput.value;
        if (!location) {
        alert('Please, input a city and country');
        return
    } else {
        try {
            preloader();
            const response = await returnObject(currentUnit, location);
            if (response.error) {
                removePreloader();
                throw { error: true, message: response.message, status: response.status };
            };
            removePreviousContainers();
            currentWeatherObject = response;
            initializeNavigation(currentWeatherObject);
            codeExecuted = true;
            errorHandled = true;
            removePreloader();
        } catch(error) {
            const errorStatus = error.status;
            status = error;
            errorBackground(errorStatus);
            removePreloader();
        };
    };
};

function removePreviousContainers() {
    const repeatedContainers = document.querySelectorAll('.error-elements-container, .alt-content-container, .scrolling-background, .error-scrolling-background');
    repeatedContainers.forEach(container => container.remove());
};

function errorBackground(errorCode) {
    removePreviousContainers();
    const pagebody = document.querySelector('body');
    const contentContainer =  document.createElement('div');
    const scrollingWrapper = document.createElement('div');
    const scrollingBackgroundleft = document.createElement('div');
    const scrollingBackgroundRight = document.createElement('div');
    const background = document.createElement('div');
    const message = document.createElement('h1');
    contentContainer.classList.add('error-elements-container');
    background.classList.add('alt-content-container');
    scrollingWrapper.classList.add('scrolling-wrapper');
    scrollingBackgroundleft.classList.add('error-scrolling-background');
    scrollingBackgroundRight.classList.add('error-scrolling-background');
    message.classList.add('error-message');
    errorCode === 400 
    ? message.textContent = 'The requests is invalid, please input a correct location'
    : errorCode === 401
    ? message.textContent = 'Weather service account status is inactive or disabled, please contact administrator'
    : errorCode === 429
    ? message.textContent = 'Too many attempts, please contact administrator or try again tomorrow'
    : message.textContent = 'Weather service unavailable, please try again later';
    background.appendChild(message);
    scrollingWrapper.append(
        scrollingBackgroundleft,
        scrollingBackgroundRight
    );
    contentContainer.append(scrollingWrapper, background)
    pagebody.append(contentContainer);

    const backgrounds = document.querySelectorAll('.error-scrolling-background');
    const viewportHeight = window.innerHeight;
    const viewportWidth = window.innerWidth;

    const imageWidth = 1920;
    const imageHeight = 1200;

    const calculatedWidth = () => {
        const width = (imageWidth / imageHeight) * viewportHeight;
        if (width > viewportWidth) {
            return width
        } else {
            return viewportWidth
        }
    };

    backgrounds.forEach((bg, index) => {
        if (calculatedWidth() === viewportWidth) {
            bg.style.width = `${calculatedWidth()}px`;
            bg.style.height = `${viewportHeight}px`;
            bg.style.left = `${index * calculatedWidth()}px`;
            bg.style.backgroundSize = 'cover';
            bg.style.backgroundPosition = 'center';
        } else {
            bg.style.width = `${calculatedWidth()}px`;
            bg.style.height = `${viewportHeight}px`;
            bg.style.left = `${index * calculatedWidth()}px`;
        };
    });

    const baseDuration = 30;
    const duration = (calculatedWidth() / viewportHeight) * baseDuration;

    backgrounds.forEach(bg => {
        bg.style.animationDuration = `${duration}s`;
    });
};

function errorFunc() {
    console.log(`ERROR: user denied permission`);
    removePreviousContainers();

    const pagebody = document.querySelector('body');
    const pageMainContainer = document.createElement('div');
    const background = document.createElement('div');
    const scrollingWrapper = document.createElement('div');
    const scrollingBackgroundLeft = document.createElement('div');
    const scrollingBackgroundRight = document.createElement('div');
    const message = document.createElement('h1');
    const searchContainer = document.createElement('div');
    const textField = document.createElement('input');
    const sendButton = document.createElement('button');

    pageMainContainer.classList.add('error-elements-container');
    scrollingWrapper.classList.add('scrolling-wrapper');
    scrollingBackgroundLeft.classList.add('scrolling-background');
    scrollingBackgroundRight.classList.add('scrolling-background');
    background.classList.add('alt-content-container');
    message.classList.add('error-message');
    message.textContent = 'Coordinates could not be obtained, search by location instead:';
    searchContainer.classList.add('search-container');
    textField.classList.add('location-input-text');
    textField.type = 'text';
    textField.placeholder = 'Type in a city and country';
    sendButton.classList.add('location-send-button');
    sendButton.addEventListener('click', searchButtonEventListener);
    textField.addEventListener('input', inputFieldFilter);

    searchContainer.append(textField, sendButton);
    background.append(message, searchContainer);
    scrollingWrapper.append(scrollingBackgroundLeft, scrollingBackgroundRight);
    pageMainContainer.append(scrollingWrapper, background);
    pagebody.appendChild(pageMainContainer);

    const backgrounds = document.querySelectorAll('.scrolling-background');
    const viewportHeight = window.innerHeight;
    const viewportWidth = window.innerWidth;

    const imageWidth = 1920;
    const imageHeight = 1200;

    const calculatedWidth = () => {
        const width = (imageWidth / imageHeight) * viewportHeight;
        if (width > viewportWidth) {
            return width
        } else {
            return viewportWidth
        }
    };

    backgrounds.forEach((bg, index) => {
        if (calculatedWidth() === viewportWidth) {
            bg.style.width = `${calculatedWidth()}px`;
            bg.style.height = `${viewportHeight}px`;
            bg.style.left = `${index * calculatedWidth()}px`;
            bg.style.backgroundSize = 'cover';
            bg.style.backgroundPosition = 'center';
        } else {
            bg.style.width = `${calculatedWidth()}px`;
            bg.style.height = `${viewportHeight}px`;
            bg.style.left = `${index * calculatedWidth()}px`;
        };
    });

    const baseDuration = 30;
    const duration = (calculatedWidth() / viewportHeight) * baseDuration;

    backgrounds.forEach(bg => {
        bg.style.animationDuration = `${duration}s`;
    });
};

function generateContent({object, system, unit, index, display}) {
    const weatherObject = object;
    const currentSystem = system;
    const currentUnit = unit;
    const currentIndex = index;
    const currentDisplay = display;

    displayPrimaryData(weatherObject, currentSystem);
    secondaryInformationButtons('.main-data-container');
    displaySecondaryData(weatherObject, currentSystem);
    dailyForecast(weatherObject, currentUnit);
    hourlyForecast(weatherObject, currentUnit);
    navigationButtons('.aditional-information-container', '.daily-forecast');
    navigationButtons('.aditional-information-container', '.hourly-forecast');
    scrollButtons('.aditional-information-container', '.daily-forecast');
    setsContainerMargin('.aditional-information', '.daily-forecast');
    setsContainerMargin('.aditional-information', '.hourly-forecast');
    windowEventListener();
    currentDisplay === 'daily' 
    ? navButtonStyle(currentIndex, '.daily-navigation-bar') 
    & scroll(currentIndex, '.daily-forecast') 
    : navButtonStyle(currentIndex, '.hourly-navigation-bar') 
    & scroll(currentIndex, '.hourly-forecast');
};

async function initializeNavigation(object) {
    let previousDisplay = currentDisplay;
    const previousUnit = currentUnit;
    const weatherObject = object;
    let previousDailyIndex = returnCurrentIndex('daily');
    let previousHourlyIndex = returnCurrentIndex('hourly');
    
    if (previousUnit === 'metric') {
        previousDisplay === 'daily' 
        ? generateContent({
            object: weatherObject, 
            system: 'metric', 
            unit: '°C', 
            index: previousDailyIndex, 
            display: 'daily'
        }) 
        : generateContent({
            object: weatherObject, 
            system: 'metric', 
            unit: '°C', 
            index: previousHourlyIndex, 
            display: 'hourly'
        });
        const mphKph = document.querySelector('.display-mph-kph');
        mphKph.textContent = 'Change units to °F/mph';
    } else {
        previousDisplay === 'daily' 
        ? generateContent({
            object: weatherObject,
            system: 'us', 
            unit: '°F', 
            index: previousDailyIndex, 
            display: 'daily'
        }) 
        : generateContent({
            object: weatherObject,
            system: 'us', 
            unit: '°F', 
            index: previousHourlyIndex, 
            display: 'hourly'
        });
        const mphKph = document.querySelector('.display-mph-kph');
        mphKph.textContent = 'Change units to °C/kph';
    };
    const dailyButtons = document.querySelectorAll('.daily-navigation-bar div');
    const hourlyButtons = document.querySelectorAll('.hourly-navigation-bar div');

    if (previousDailyIndex >= dailyButtons.length) {
        previousDailyIndex = dailyButtons.length - 1;
    };
    if (previousHourlyIndex >= hourlyButtons.length) {
        previousHourlyIndex = hourlyButtons.length - 1;
    };
    previousDailyIndex = Math.max(0, previousDailyIndex);
    previousHourlyIndex = Math.max(0, previousHourlyIndex);

    if (previousDisplay === 'hourly') {
        const hourlyNavBar = document.querySelector('.hourly-navigation-bar');
        const dailyNavBar = document.querySelector('.daily-navigation-bar');
        const dailyHourlySwitch = document.querySelector('.display-daily-hourly');
        const dailyForecastContainer = document.querySelector('.daily-forecast');
        const hourlyForecastContainer = document.querySelector('.hourly-forecast');
        dailyHourlySwitch.textContent = 'Display daily forecast';
        dailyNavBar.classList.add('hide-element');
        dailyNavBar.classList.remove('display-element');
        hourlyNavBar.classList.add('display-element');
        hourlyNavBar.classList.remove('hide-element');
        if (dailyForecastContainer.classList.contains('display-element')) {
            dailyForecastContainer.classList.remove('display-element');
        };
        if (!dailyForecastContainer.classList.contains('hide-element')) {
            dailyForecastContainer.classList.add('hide-element')
        };
        if (!hourlyForecastContainer.classList.contains('display-element')) {
            hourlyForecastContainer.classList.add('display-element');
        };
        scroll(previousHourlyIndex, '.hourly-forecast');
    } else {
        const hourlyNavBar = document.querySelector('.hourly-navigation-bar');
        const dailyNavBar = document.querySelector('.daily-navigation-bar');
        const hourlyForecastContainer = document.querySelector('.hourly-forecast');
        const dailyForecastContainer = document.querySelector('.daily-forecast');
        dailyNavBar.classList.add('display-element');
        dailyNavBar.classList.remove('hide-element');
        hourlyNavBar.classList.add('hide-element');
        hourlyNavBar.classList.remove('display-element');
        if (hourlyForecastContainer.classList.contains('display-element')) {
            hourlyForecastContainer.classList.remove('display-element');
            hourlyForecastContainer.classList.add('hide-element');
        };
        dailyForecastContainer.classList.add('display-element');
        scroll(previousDailyIndex, '.daily-forecast');
    };
    const weatherDescription = weatherObject.currentConditions.icon;
    const currentScreenOrientation = getOrientation();
    const imgUrl = returnImageUrl(weatherDescription, currentScreenOrientation);
    setBackground(imgUrl);
    creditButton();
    codeExecuted = true;
};

function updateCurrentWeatherObject() {
    if (updatedWeatherObject) {
        currentWeatherObject = updatedWeatherObject;
    };
};

async function main() {
    if (!codeExecuted) {
        preloader()
    };
    updateCurrentWeatherObject();
    if (status.error) {
        errorBackground(status.status);
        removePreloader();
        return;
    };
    if (codeExecuted && currentWeatherObject) {
        const weatherObject = currentWeatherObject;
        initializeNavigation(weatherObject);
        removePreloader();
        return
    };
    const coords = await getCoordinates();
    if (coords) {
        try {
            const location = await getAddress(coords);
            const weatherObject = await returnObject(currentUnit, location);
            if (!weatherObject.ok) {
                const error = new Error(weatherObject.status);
                error.status = weatherObject.status;
                throw error
            };
            currentWeatherObject = weatherObject;
            initializeNavigation(currentWeatherObject);
            removePreloader();
            return
        } catch (error) {
            errorBackground(error.message);
            removePreloader();
            return
        };
    } else if (!errorHandled && !codeExecuted) {
        errorFunc();
        removePreloader();
    };
    removePreloader();
};

export { initializeNavigation, main }