import { scroll, scrollButtons, returnCurrentIndex } from './interactionModule.js';
import { preloader, removePreloader, displayPrimaryData, displaySecondaryData, setsContainerMargin, navButtonStyle } from './renderModule.js';
import { dailyForecast, hourlyForecast, navigationButtons } from './appElementsModule.js';
import { returnObject, getWeather } from './weatherModule.js';
import { initializeNavigation } from './mainLogicModule.js';

let currentDisplay = 'daily';
let currentUnit = 'metric';
let dailyIndex = 0;
let hourlyIndex = 0;
let bodyElement = document.body;
let eventDelegationClosure = null;
let updatedWeatherObject = null;

function windowEventListener() {
    if (bodyElement && eventDelegationClosure) {
        bodyElement.removeEventListener('click', eventDelegationClosure)
    };
    eventDelegationClosure = eventDelegation();
    bodyElement.addEventListener('click', eventDelegationClosure)
}

function renderContent(object) {
    initializeNavigation(object);
};

function eventDelegation () {
    const buttons = document.querySelector('.unit-buttons');
    return async function (event) {
        const mphKph = document.querySelector('.display-mph-kph');
        const dailyHourly = document.querySelector('.display-daily-hourly');
        const target = event.target;
        const weatherObjectMetric = await returnObject('metric');
        const weatherObjectUs = await returnObject('us');

        if (target.classList.contains('display-daily-hourly')) {
            const hourlyNavBar = document.querySelector('.hourly-navigation-bar');
            const dailyNavBar = document.querySelector('.daily-navigation-bar');
            const dailyForecastContainer = document.querySelector('.daily-forecast');
            const hourlyForecastContainer = document.querySelector('.hourly-forecast');
            if (dailyHourly.textContent === 'Display hourly forecast') {
                dailyHourly.textContent = 'Display daily forecast';
                currentDisplay = 'hourly';
                dailyNavBar.classList.remove('display-element');
                dailyNavBar.classList.add('hide-element');
                dailyForecastContainer.classList.remove('display-element');
                dailyForecastContainer.classList.add('hide-element');
                hourlyForecastContainer.classList.add('display-element');
                hourlyForecastContainer.classList.remove('hide-element');
                hourlyNavBar.classList.add('display-element');
                hourlyNavBar.classList.remove('hide-element');
                scrollButtons('.aditional-information-container', '.hourly-forecast');
                navButtonStyle(returnCurrentIndex('hourly'), '.hourly-navigation-bar');
            } else {
                dailyHourly.textContent = 'Display hourly forecast';
                currentDisplay = 'daily';
                dailyNavBar.classList.add('display-element');
                dailyNavBar.classList.remove('hide-element');
                dailyForecastContainer.classList.remove('hide-element');
                dailyForecastContainer.classList.add('display-element');
                hourlyForecastContainer.classList.add('hide-element');
                hourlyForecastContainer.classList.remove('display-element');
                hourlyNavBar.classList.remove('display-element');
                hourlyNavBar.classList.add('hide-element');
                scrollButtons('.aditional-information-container', '.daily-forecast');
                navButtonStyle(returnCurrentIndex('daily'), '.daily-navigation-bar');
            };
            setsContainerMargin('.aditional-information', '.hourly-forecast');
            setsContainerMargin('.aditional-information', '.daily-forecast');
        };
        if (target.classList.contains('display-mph-kph')) {
            const primaryData = document.querySelector('.primary-data-content');
            const secondaryData = document.querySelector('.secondary-data-container');
            const hourlyData = document.querySelector('.hourly-forecast');
            const dailyData = document.querySelector('.daily-forecast');
            const containers = [
                primaryData, 
                secondaryData, 
                hourlyData, 
                dailyData
            ];
            if (mphKph.textContent === 'Change units to °F/mph') {
                mphKph.textContent = 'Change units to °C/kph';
                currentUnit = 'us';
                containers.forEach(container => {
                    container.remove();
                });
                console.log('changing units')
                displayPrimaryData(weatherObjectUs, 'us');
                displaySecondaryData(weatherObjectUs, 'us');
                hourlyForecast(weatherObjectUs, '°F');
                dailyForecast(weatherObjectUs, '°F');
                navigationButtons('.aditional-information-container', '.daily-forecast');
                navigationButtons('.aditional-information-container', '.hourly-forecast');
                scrollButtons('.aditional-information-container', '.hourly-forecast');
                scrollButtons('.aditional-information-container', '.daily-forecast');
                const dailyForecastContainer = document.querySelector('.daily-forecast');
                const hourlyForecastContainer = document.querySelector('.hourly-forecast');
                const dailyNavBarContainer = document.querySelector('.daily-navigation-bar');
                const hourlyNavBarContainer = document.querySelector('.hourly-navigation-bar');
                if (currentDisplay === 'hourly') {
                    dailyForecastContainer.classList.add('hide-element');
                    hourlyForecastContainer.classList.add('display-element');
                    dailyNavBarContainer.classList.add('hide-element');
                    dailyNavBarContainer.classList.remove('display-element');
                    hourlyNavBarContainer.classList.add('display-element');
                    hourlyNavBarContainer.classList.remove('hide-element');
                    setsContainerMargin('.aditional-information', '.hourly-forecast');
                    navButtonStyle(returnCurrentIndex('hourly'), '.hourly-navigation-bar');
                } else if (currentDisplay === 'daily') {
                    dailyForecastContainer.classList.remove('hide-element');
                    hourlyForecastContainer.classList.remove('display-element');
                    dailyNavBarContainer.classList.add('display-element');
                    dailyNavBarContainer.classList.remove('hide-element')
                    hourlyNavBarContainer.classList.add('hide-element');
                    hourlyNavBarContainer.classList.remove('display-element');
                    setsContainerMargin('.aditional-information', '.daily-forecast');
                    navButtonStyle(returnCurrentIndex('daily'), '.daily-navigation-bar');
                };
            } else if (mphKph.textContent === 'Change units to °C/kph') {
                mphKph.textContent = 'Change units to °F/mph';
                currentUnit = 'metric';
                containers.forEach(container => {
                    container.remove();
                });
                console.log('changing to units (metric)')
                displayPrimaryData(weatherObjectMetric, 'metric');
                displaySecondaryData(weatherObjectMetric, 'metric');
                hourlyForecast(weatherObjectMetric, '°C');
                dailyForecast(weatherObjectMetric, '°C');
                navigationButtons('.aditional-information-container', '.daily-forecast');
                navigationButtons('.aditional-information-container', '.hourly-forecast');
                scrollButtons('.aditional-information-container', '.hourly-forecast');
                scrollButtons('.aditional-information-container', '.daily-forecast');
                const dailyForecastContainer = document.querySelector('.daily-forecast');
                const hourlyForecastContainer = document.querySelector('.hourly-forecast');
                const dailyNavBarContainer = document.querySelector('.daily-navigation-bar');
                const hourlyNavBarContainer = document.querySelector('.hourly-navigation-bar');
                if (currentDisplay === 'hourly') {
                    dailyForecastContainer.classList.add('hide-element');
                    hourlyForecastContainer.classList.add('display-element');
                    dailyNavBarContainer.classList.remove('display-element');
                    dailyNavBarContainer.classList.add('hide-element');
                    hourlyNavBarContainer.classList.add('display-element');
                    hourlyNavBarContainer.classList.remove('hide-element');
                    setsContainerMargin('.aditional-information', '.hourly-forecast');
                    navButtonStyle(returnCurrentIndex('hourly'), '.hourly-navigation-bar');
                } else if (currentDisplay === 'daily') {
                    dailyForecastContainer.classList.remove('hide-element');
                    hourlyForecastContainer.classList.remove('display-element');
                    dailyNavBarContainer.classList.add('display-element');
                    dailyNavBarContainer.classList.remove('hide-element')
                    hourlyNavBarContainer.classList.add('hide-element');
                    hourlyNavBarContainer.classList.remove('display-element');
                    setsContainerMargin('.aditional-information', '.daily-forecast');
                    navButtonStyle(returnCurrentIndex('daily'), '.daily-navigation-bar');
                };
            };
        };
        if (target.classList.contains('left-button') && currentDisplay == 'daily') {
            const setsContainer = document.querySelector('.daily-forecast');
            const navBarClassChildren = setsContainer.children;
            dailyIndex = (returnCurrentIndex('daily') -1 + navBarClassChildren.length) % navBarClassChildren.length;
            scroll(dailyIndex, '.daily-forecast');
        } else if ((target.classList.contains('left-button') && currentDisplay == 'hourly')) {
            const setsContainer = document.querySelector('.hourly-forecast');
            const navBarClassChildren = setsContainer.children;
            hourlyIndex = (returnCurrentIndex('hourly') -1 + navBarClassChildren.length) % navBarClassChildren.length;
            scroll(hourlyIndex, '.hourly-forecast');
        };
        if (target.classList.contains('right-button')) {
            if (currentDisplay == 'daily') {
                const setsContainer = document.querySelector('.daily-forecast');
                const navBarClassChildren = setsContainer.children;
                dailyIndex = (returnCurrentIndex('daily') +1 + navBarClassChildren.length) % navBarClassChildren.length;
                scroll(dailyIndex, '.daily-forecast');
            } else if (currentDisplay == 'hourly') {
                const setsContainer = document.querySelector('.hourly-forecast');
                const navBarClassChildren = setsContainer.children;
                hourlyIndex = (returnCurrentIndex('hourly') +1 + navBarClassChildren.length) % navBarClassChildren.length;
                scroll(hourlyIndex, '.hourly-forecast');
            };
        };
        if (target.classList.contains('navigation-button')){
            if (currentDisplay === 'daily') {
                const buttons = [...document.querySelectorAll('.daily-navigation-bar .navigation-button')];
                const buttonIndex = buttons.indexOf(target);
                if (buttonIndex !== -1 ) {
                    scroll(buttonIndex, '.daily-forecast');
                }
            } else {
                const buttons = [...document.querySelectorAll('.hourly-navigation-bar .navigation-button')];
                const buttonIndex = buttons.indexOf(target);
                if (buttonIndex !== -1) {
                    scroll(buttonIndex, '.hourly-forecast');
                }
            };
        };
        if (target.classList.contains('search-button')) {
            const textInput = document.querySelector('.location-field');
            const location = textInput.value;
            if (!location) {
                alert('Please, input a city and country');
                return
            };
            preloader();
            const weatherObject = await getWeather(location, currentUnit, null);
            updatedWeatherObject = weatherObject;
            renderContent(updatedWeatherObject);
            removePreloader();
        };
        return
    };
};

export { windowEventListener, currentUnit, currentDisplay, updatedWeatherObject };